import Slider from "react-slick";
import "./slick.scss";

const Slick = () => {
    const Arrow = (props) => {
        const { onClick, text, classN } = props;

        return (
            <button className={classN} onClick={onClick}>{text}</button>
        )
    }

    let settings = {
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        nextArrow : <Arrow text={<img src="images/arrow.svg" alt="" />} classN="next"/>,
    }
    return (
        <Slider {...settings}>
            <div className="slider-img">
                <img src="images/slider-1.jpg" alt="" />
            </div>
            <div className="slider-img">
                <img src="images/slider-1.jpg" alt="" />
            </div>
        </Slider>
    );
}
 
export default Slick;